.pic-left-box {
  font-size: 22px;
  text-align: center;
  font-family: Arial ;
  color: #ffffff;
  padding-bottom: 0%; 
  padding-top: 0%;
  flex: 20%;
  margin-left: 10px;
  border-radius: 10px;
