  flex: 50%;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgb(7, 27, 51);
  box-sizing: border-box;
  margin-bottom: 10px;
  margin-right: 10px;

  
}
.right-box {
  font-family: 'Arial';
  font-size: 22px;
  text-align: left;
