  padding: 6%;
  box-sizing: border-box;
  flex: 40%;
  margin-left: 10px;
  border-radius: 30px;
  box-shadow: 1px 1px 5px rgb(7, 27, 51);
  color: #ffffff;
  margin-bottom: 10px;
  margin-right: 10px;


}

.pic-right-box {
  font-family: 'Arial';
