  }
}

.flex-box{
  display: flex;
  flex-wrap: wrap;
}

.Moving-name{
  font-family: 'Times New Roman', Times, serif;
  font-size: 18px;
  color: black;
  position: relative;
  animation: movementname 8s infinite;
  padding-top: 16px;
  font-style: italic;
  box-shadow: 0 0 1px #347891;

}

@keyframes movementname {
  from {
    left:0px;

  }
  to {
    left: 45px;
  }
}

.App {
  text-align: left;
}
.App_center {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.bar-top{
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  background-color:#347891;
}

.App-header {
  background-color: #1e414e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
}



.Box{
  padding: 15px;
  border-radius: 50px ;
  margin-right: 10px;
  color: rgb(255, 248, 248);
  text-decoration: none;

}
.Box:hover{
  padding: 15px;
  border-radius: 50px ;
  margin-right: 10px;
  text-decoration: underline;

}

.a1{
  color: rgb(255, 248, 248);
  text-decoration: none;

}
.a1:hover{
  text-decoration: underline;

}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bottom-button {
  font-size: 50px;
  text-align: center;
  font-family: Arial;
  color: rgba(0, 0, 0, 0.567);
  padding-bottom: 1%; 
  padding-top: 1%;
  flex: 50%;
  margin-left: 40%;
  box-sizing: border-box;
  margin-bottom: 5%;
  position: fixed;
  bottom: 4%;
  width: 15%;
  height: 10%;
  border-radius: 60%;
  
}
@media (max-width: 1200px) {
  .bottom-button  {
    flex: 100%;
  }
}

.bottom-button:hover{
  color: rgb(202, 202, 199);

}
